.jobs-header {
    width: 100%;
    background-color: whitesmoke;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}

.job-conpany-title {
    color: black;
}

.company-logo {
    width: 80px;
    height: 80px;
}
.header-company {
    margin-left: 5vw;
    margin-top: 10px;
    margin-bottom: 10px;
}

.background {
    background: #e3e3e3;
}

.jobs-container {
    box-sizing: border-box;
    width: 95%;
    max-width: 300px;
    margin: 20px 10px;
    background-color: whitesmoke;
    padding: 10px 20px;
    border-radius: 12px;
}

.jobs-title {
    padding-top: 10px;
    padding-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

hr {
    border: solid black 0.3 px;
}
.jobs-item-container {
    padding: 20px 10px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}
.jobs-item-container > * {
    margin: 2px 0;
}
.jobs-item-title {
    font-size: 18px;
    cursor: pointer;
}

@media screen and (max-width: 400px) {
    .jobs-container {
        padding: 10px 10px;
        width: 90%;
    }
}

.flex-row-center {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
}

.line {
    border-bottom: solid black 5px;
    width: 70%;
    justify-self: center;
    align-self: center;
    margin: 0 auto;
}

.highlight-bars {
    position: fixed;
    z-index: -9;
    bottom: 0px;
    width: 800px;
    height: 800px;
    right: 0px;
}

.job-flex {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}

.job-description-container {
    box-sizing: border-box;
    width: 95%;
    max-width: 600px;
    min-width: 350px;
    margin: 20px 10px;
    background-color: whitesmoke;
    padding: 10px 20px;
    border-radius: 12px;
    height: 500px;
    overflow-y: scroll;
    padding-bottom: 40px;
}

@media screen and (max-width: 400px) {
    .job-description-container {
        box-sizing: border-box;
        width: 95%;
        max-width: 600px;
        margin: 20px 10px;
        background-color: whitesmoke;
        padding: 10px 20px;
        border-radius: 12px;
        height: 500px;
    }
}

@media screen and (max-width: 700px) {
    ::-webkit-scrollbar {
        display: none;
    }
}

.job-description-close {
    width: 25px;
    height: 25px;
    margin-top: 8px;
    cursor: pointer;
}

.job-description-modal-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: #1290caaa;
}

.job-description-modal-content {
    box-sizing: border-box;
    width: 95%;
    margin: 20px 10px;
    background-color: whitesmoke;
    padding: 10px 20px;
    border-radius: 12px;
    height: 80vh;
    margin: 10vh auto;
    overflow-y: scroll;
}

.job-description {
    margin-top: 20px;
}

::-webkit-scrollbar {
    width: 20px;
}

::-webkit-scrollbar-track {
    background-color: transparent;
}

::-webkit-scrollbar-thumb {
    background-color: #d6dee1;
    border-radius: 20px;
    border: 6px solid transparent;
    background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #a8bbbf;
}
